.home {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.headerContainer {
    width: auto;
    margin-left: 30px;
}

.home .headerContainer p {
    margin-top: 15px;
    padding: 0;
    font-size: 50px;
    font-weight: lighter;
    color: darkgoldenrod;
}

.home .headerContainer button {
    background-color: black;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    height: 50px;
    width: 180px;
    text-decoration: none;
    color: white;
    font-size: 20px;    
}

.headerContainer button:hover {
    background-color: darkred;
    cursor: pointer;
    transition: 0.3s ease-in;
}

/* mobile view */

@media only screen and (max-width:600px) {
    .home {
        justify-content: center;
        align-items: center;
    }
    .headerContainer{
        margin-left: 0;
        border-radius: 10px;
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: purple;
        color: white; 
    }
    .home.headerContainer h1 {
        font-size: 40px;
        height: 30px;
        margin: 15px 0px;
        color: white;
    }
}